@charset "UTF-8";
/* reset */
body, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, th, td, form, fieldset, legend, input, textarea, button, select {
  margin: 0;
  padding: 0; }

body, h1, h2, h3, h4, h5, h6, input, button, table, select, textarea {
  color: #333;
  font-family: Sans-serif;
  font-size: 14px;
  line-height: 1.6em;
  letter-spacing: -0.05em;
  word-break: break-all;
  -webkit-text-size-adjust: none; }

img, fieldset, iframe, blockquote {
  border: 0 none;
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

input, select, button {
  border: 0 none;
  background: none;
  vertical-align: middle; }

img {
  vertical-align: top; }

i, em, address {
  font-style: normal; }

label, button {
  cursor: pointer; }

button {
  margin: 0;
  padding: 0;
  border: 0; }

hr {
  display: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

input[type=checkbox], input[type=radio] {
  border: 0 none; }

a {
  color: #333;
  text-decoration: none; }

* {
  -webkit-tap-highlight-color: rgba(41, 182, 246, 0.3); }

/* html5 */
article, aside, audio, canvas, details, figcaption, figure, footer, header, hgroup, main, menu, nav, output, section, video {
  display: block; }

/* video */
video::-internal-media-controls-download-button {
  display: none; }

video::-webkit-media-controls-enclosure {
  overflow: hidden; }

video::-webkit-media-controls-panel {
  width: calc(100% + 30px); }

